import React from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom'
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Home from './pages/Home';
import Header from './components/Header';
import Footer from './components/Footer';
import Contact from './pages/Contact';
import About from './pages/About';
import Shop from './pages/Shop';
import Services from './pages/Services';
import Designs from './pages/Designs';

function App() {
  return (
    <div className="App">
      <Header/>
      <Router>
        <Route exact path="/" component={Home}/>
        <Route path="/FlowerMan/about" component={About}/>
        <Route path="/FlowerMan/contact" component={Contact}/>
        <Route path="/FlowerMan/shop" component={Shop}/>
        <Route path="/FlowerMan/designs" component={Designs}/>
        <Route path="/FlowerMan/services" component={Services}/>
      </Router>
      <Footer/>
    </div>
  );
}

export default App;
